<template>
  <article class="price-pop">
    <div class="price-input">
      <section class="price-input-item">
        <div class="common-price-input">
          <span class="input-text">价格</span>
          <div class="input-price new-price" @click="inputFocus(0)">
            {{ price }}
            <span class="cursor-blink" v-show="blinkShow === 0"></span>
          </div>
        </div>
      </section>
      <section class="price-input-item">
        <div class="common-price-input">
          <span class="input-text">原价</span>
          <div class="input-price" @click="inputFocus(1)">
            {{ oldPrice }}
            <span class="cursor-blink" v-show="blinkShow === 1"></span>
          </div>
        </div>
      </section>
      <section class="price-input-item">
        <div class="common-price-input">
          <span class="input-text">运费</span>
          <div class="other-input-price" @click="inputFocus(2)">
            {{ mailCharge }}
            <span class="cursor-blink" v-show="blinkShow === 2" v-if="!isShowMail"></span>
          </div>
          <checkbox v-model="isMailCharge" :disabled="isDisabled == 1">包邮</checkbox>
        </div>
      </section>
    </div>
    <number-keyboard
      :transition="false"
      :show="true"
      theme="custom"
      extra-key="."
      close-button-text="完成"
      @close="onClose"
      @blur="show = false"
      @input="onInput"
      @delete="onDelete"
      class="keybord"
      :hide-on-click-outside="false"
    />
  </article>
</template>

<script lang="ts">
import { ref, watch, defineComponent, watchEffect } from 'vue';
import { NumberKeyboard, Checkbox } from 'vant';
export default defineComponent({
  components: {
    NumberKeyboard,
    Checkbox
  },
  props: {
    allPrice: {
      type: Object,
      default: () => {
        return {};
      }
    },
    isShowMail: {
      type: Boolean,
      default: false
    }
  },
  emits: ['setPriceData'],
  setup(props, { emit }) {
    const isDisabled = ref(0);
    const blinkShow = ref(0);
    const isMailCharge = ref(false);
    const price = ref('');
    const oldPrice = ref('');
    const mailCharge = ref('');
    if (props.allPrice) {
      price.value = props.allPrice.price as string;
      oldPrice.value = props.allPrice.oldPrice as string;
      mailCharge.value = props.allPrice.mailCharge as string;
      if (props.allPrice.mailCharge === '包邮') {
        isMailCharge.value = true;
      }
    }
    //判断自提和送货上门包邮
    watchEffect(() => {
      if (props.isShowMail) {
        isMailCharge.value = true;
        isDisabled.value = 1;
      } else {
        isMailCharge.value = false;
        isDisabled.value = 0;
      }
    });

    const inputFocus = (index: number) => {
      blinkShow.value = index;
    };
    watch(isMailCharge, (newValue) => {
      if (newValue) {
        mailCharge.value = '包邮';
      } else {
        mailCharge.value = '';
      }
    });
    watch(mailCharge, (newValue) => {
      if (newValue != '包邮') {
        isMailCharge.value = false;
      } else {
        isMailCharge.value = true;
      }
    });
    const onInput = (key: number) => {
      switch (blinkShow.value) {
        case 0:
          price.value = addPrice(price.value, key);
          break;
        case 1:
          oldPrice.value = addPrice(oldPrice.value, key);
          break;
        case 2:
          if (!props.isShowMail) {
            mailCharge.value = addPrice(mailCharge.value, key);
          }
          break;
      }
    };
    const onDelete = () => {
      switch (blinkShow.value) {
        case 0:
          price.value = '';
          break;
        case 1:
          oldPrice.value = '';
          break;
        case 2:
          mailCharge.value = '';
          break;
      }
    };
    //用户输入价格完成
    const onClose = () => {
      const allPrice = {
        mailCharge: mailCharge.value,
        price: price.value,
        oldPrice: oldPrice.value
      };
      emit('setPriceData', allPrice);
    };
    const addPrice = (price: string, key: number): string => {
      return (price = price + key);
      // return price
      //   .replace(/[^\d.]/g, '')
      //   .replace(/\.{2,}/g, '.')
      //   .replace('.', '$#$')
      //   .replace(/\./g, '')
      //   .replace('$#$', '.')
      //   .replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3')
      //   .replace(/^\./g, '');
    };
    return {
      blinkShow,
      inputFocus,
      price,
      oldPrice,
      mailCharge,
      onInput,
      onDelete,
      isMailCharge,
      onClose,
      isDisabled
    };
  }
});
</script>

<style lang="scss" scoped>
.price-pop {
  .price-input {
    background-color: #f2f3f5;
    .price-input-item {
      padding: 0 10px;
      height: 40px;
      line-height: 40px;
      background-color: #fff;
      font-size: 13px;
      .common-price-input {
        height: 100%;
        border-bottom: 1px solid #f2f3f5;
        display: flex;
        position: relative;
        .input-text {
          height: 40px;
        }
        .new-price {
          font-size: 16px;
          color: red;
        }
        .input-price,
        .other-input-price {
          display: inline-block;
          margin-left: 10px;
          width: 80%;
          height: 100%;
          .cursor-blink {
            animation: blink 1s infinite steps(1, start);
            width: 1px;
            height: 20px;
            display: inline-block;
            position: relative;
            top: 5px;
            left: 0;
            @keyframes blink {
              0%,
              100% {
                background-color: #000;
                color: #aaa;
              }
              50% {
                background-color: #bbb; /* not #aaa because it's seem there is Google Chrome bug */
                color: #000;
              }
            }
          }
        }
        .other-input-price {
          width: 70%;
        }
      }
      .radio-mail {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
  }
  .keybord {
    position: relative;
  }
}
</style>
