
import { ref, watch, defineComponent, watchEffect } from 'vue';
import { NumberKeyboard, Checkbox } from 'vant';
export default defineComponent({
  components: {
    NumberKeyboard,
    Checkbox
  },
  props: {
    allPrice: {
      type: Object,
      default: () => {
        return {};
      }
    },
    isShowMail: {
      type: Boolean,
      default: false
    }
  },
  emits: ['setPriceData'],
  setup(props, { emit }) {
    const isDisabled = ref(0);
    const blinkShow = ref(0);
    const isMailCharge = ref(false);
    const price = ref('');
    const oldPrice = ref('');
    const mailCharge = ref('');
    if (props.allPrice) {
      price.value = props.allPrice.price as string;
      oldPrice.value = props.allPrice.oldPrice as string;
      mailCharge.value = props.allPrice.mailCharge as string;
      if (props.allPrice.mailCharge === '包邮') {
        isMailCharge.value = true;
      }
    }
    //判断自提和送货上门包邮
    watchEffect(() => {
      if (props.isShowMail) {
        isMailCharge.value = true;
        isDisabled.value = 1;
      } else {
        isMailCharge.value = false;
        isDisabled.value = 0;
      }
    });

    const inputFocus = (index: number) => {
      blinkShow.value = index;
    };
    watch(isMailCharge, (newValue) => {
      if (newValue) {
        mailCharge.value = '包邮';
      } else {
        mailCharge.value = '';
      }
    });
    watch(mailCharge, (newValue) => {
      if (newValue != '包邮') {
        isMailCharge.value = false;
      } else {
        isMailCharge.value = true;
      }
    });
    const onInput = (key: number) => {
      switch (blinkShow.value) {
        case 0:
          price.value = addPrice(price.value, key);
          break;
        case 1:
          oldPrice.value = addPrice(oldPrice.value, key);
          break;
        case 2:
          if (!props.isShowMail) {
            mailCharge.value = addPrice(mailCharge.value, key);
          }
          break;
      }
    };
    const onDelete = () => {
      switch (blinkShow.value) {
        case 0:
          price.value = '';
          break;
        case 1:
          oldPrice.value = '';
          break;
        case 2:
          mailCharge.value = '';
          break;
      }
    };
    //用户输入价格完成
    const onClose = () => {
      const allPrice = {
        mailCharge: mailCharge.value,
        price: price.value,
        oldPrice: oldPrice.value
      };
      emit('setPriceData', allPrice);
    };
    const addPrice = (price: string, key: number): string => {
      return (price = price + key);
      // return price
      //   .replace(/[^\d.]/g, '')
      //   .replace(/\.{2,}/g, '.')
      //   .replace('.', '$#$')
      //   .replace(/\./g, '')
      //   .replace('$#$', '.')
      //   .replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3')
      //   .replace(/^\./g, '');
    };
    return {
      blinkShow,
      inputFocus,
      price,
      oldPrice,
      mailCharge,
      onInput,
      onDelete,
      isMailCharge,
      onClose,
      isDisabled
    };
  }
});
